.tab {
  width: 100%;
  height: 100vh;
  .tab_contaner {
    height: max-content;
    width: 100%;
    display: flex;
    align-items: center;
    border-radius: 25px;
    padding: 0px 25px 0px 25px;
    box-sizing: border-box;
    overflow-x: scroll;
    gap: 5px;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px,
      rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none;
    @include mobile {
      overflow-x: scroll;
    }
    &::-webkit-scrollbar {
      display: none;
    }
    .item {
      height: 50px;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 5px;
      box-sizing: border-box;
      cursor: pointer;
      @include mobile {
        flex-shrink: 0;
      }
      &:hover {
        background-color: rgba(51, 197, 255, 0.2);
      }
      &.selected {
        // background-color: rgba(51, 146, 255, 1);
        border-bottom: 2px solid rgba(51, 146, 255, 1);
        color: rgba(51, 146, 255, 1);
      }
    }
  }
  .tab_page {
    height: 100vh;
  }
}
