.logContent {
  width: 600px;
  height: 500px;
  background-color: $color-sidebar;
  border-radius: 25px;
  display: flex;
  flex-direction: row;
  @include mobile {
    width: 80%;
    height: 60%;
    transition: $transition-normal;
  }
  &__img {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    img {
      width: 300px;
    }
    @include mobile {
      display: none;
      transition: $transition-normal;
    }
  }
  &__form {
    width: 300px;
    margin: 10px;
    border-radius: 0px 25px 25px 0px;
    background-color: rgb(255, 255, 255);
    @include mobile {
      width: 100%;
      border-radius: 25px;
      transition: $transition-normal;
    }
  }
}
